module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-dropdown-dropdown-button',\r\n  template: `\r\n    <div style=\"height: 28px;\">\r\n      <nz-dropdown-button (nzClick)=\"log()\">\r\n        DropDown\r\n        <ul nz-menu>\r\n          <li nz-menu-item>1st menu item</li>\r\n          <li nz-menu-item>2nd menu item</li>\r\n          <li nz-submenu>\r\n            <span title>sub menu</span>\r\n            <ul>\r\n              <li nz-menu-item>3rd menu item</li>\r\n              <li nz-menu-item>4th menu item</li>\r\n            </ul>\r\n          </li>\r\n        </ul>\r\n      </nz-dropdown-button>\r\n      <nz-dropdown-button nzDisabled>\r\n        DropDown\r\n        <ul nz-menu>\r\n          <li nz-menu-item>\r\n            <a>1st menu item</a>\r\n          </li>\r\n          <li nz-menu-item>\r\n            <a>2nd menu item</a>\r\n          </li>\r\n          <li nz-menu-item>\r\n            <a>3rd menu item</a>\r\n          </li>\r\n        </ul>\r\n      </nz-dropdown-button>\r\n      <nz-dropdown>\r\n        <button nz-button nz-dropdown><span>Button</span> <i class=\"anticon anticon-down\"></i></button>\r\n        <ul nz-menu>\r\n          <li nz-menu-item>\r\n            <a>1st menu item</a>\r\n          </li>\r\n          <li nz-menu-item>\r\n            <a>2nd menu item</a>\r\n          </li>\r\n          <li nz-menu-item>\r\n            <a>3rd menu item</a>\r\n          </li>\r\n        </ul>\r\n      </nz-dropdown>\r\n    </div>\r\n  `,\r\n  styles  : [\r\n      `\r\n      nz-dropdown-button {\r\n        margin-right: 8px;\r\n      }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoDropdownDropdownButtonComponent {\r\n  log(): void {\r\n    console.log('click dropdown button');\r\n  }\r\n}\r\n"